@import url(https://fonts.googleapis.com/css?family=Lato:300,400,700);
@import url(https://fonts.googleapis.com/css?family=Anton);
* {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body {
  font-family: "Lato", "Helvetica Neue", sans-serif;
  background-color: #f4f4f7;
  margin: 0;
}

h1 {
  font-family: "Anton", sans-serif;
}

a.link-unstyled {
  color: inherit;
}

a {
  text-decoration: none;
}

a.link-unstyled:hover {
  color: inherit;
  text-decoration: none;
}

.list-unstyled {
  list-style: none;
  color: #000;
  color: initial;
}

.btn {
  padding: 0.375rem 2rem;
  font-size: 1rem;
  margin: 0.5rem;
}

.btn.btn-primary {
  color: #fff;
  background-color: rgb(0, 132, 255);
  border-radius: 5px;
  border-color: rgb(0, 132, 255);
  cursor: pointer;
}

.btn.btn-primary:not(:disabled):not(.disabled):hover {
  color: #fff;
  background-color: rgb(0, 132, 255);
  border-color: rgb(0, 132, 255);
}

.btn.btn-primary:not(:disabled):not(.disabled):active {
  color: #fff;
  background-color: rgb(0, 132, 255);
  border-color: rgb(0, 132, 255);
}

.btn.btn-primary:not(:disabled):not(.disabled):focus {
  box-shadow: 0 0 0 0.2rem rgba(10, 60, 205, 0.5);
}

.text-danger {
  color: #cd4040;
}

.btn.btn-danger {
  color: #fff;
  background-color: #cd4040;
  border-radius: 5px;
  border-color: #cd4040;
  cursor: pointer;
}

.btn.btn-danger:not(:disabled):not(.disabled):hover {
  color: #fff;
  background-color: #cd4040;
  border-color: #cd4040;
}

.btn.btn-danger:not(:disabled):not(.disabled):active {
  color: #fff;
  background-color: #cd4040;
  border-color: #cd4040;
}

.btn.btn-danger:not(:disabled):not(.disabled):focus {
  box-shadow: 0 0 0 0.2rem rgba(255, 64, 64, 0.5);
}

.navbar {
  width: 100%;
  padding: 0.5rem 0;
  background-color: #266dd3;
}

.navbar__brand {
  color: #ffffff;

  display: flex;
  align-items: center;
}

.navbar__brand:hover {
  color: #ffffff;
  text-decoration: none;
}

.navbar__brand-logo {
  width: 100px;
  border-radius: 10px;
  margin-right: 0.5rem;
  margin-left: 0.5rem;
}

.lds-grid {
  display: inline-block;
  position: relative;
  width: 64px;
  height: 64px;
}

.lds-grid div {
  position: absolute;
  width: 13px;
  height: 13px;
  border-radius: 50%;
  background: #000;
  -webkit-animation: lds-grid 1.2s linear infinite;
          animation: lds-grid 1.2s linear infinite;
}

.lds-grid div:nth-child(1) {
  top: 6px;
  left: 6px;
  -webkit-animation-delay: 0s;
          animation-delay: 0s;
}

.lds-grid div:nth-child(2) {
  top: 6px;
  left: 26px;
  -webkit-animation-delay: -0.4s;
          animation-delay: -0.4s;
}

.lds-grid div:nth-child(3) {
  top: 6px;
  left: 45px;
  -webkit-animation-delay: -0.8s;
          animation-delay: -0.8s;
}

.lds-grid div:nth-child(4) {
  top: 26px;
  left: 6px;
  -webkit-animation-delay: -0.4s;
          animation-delay: -0.4s;
}

.lds-grid div:nth-child(5) {
  top: 26px;
  left: 26px;
  -webkit-animation-delay: -0.8s;
          animation-delay: -0.8s;
}

.lds-grid div:nth-child(6) {
  top: 26px;
  left: 45px;
  -webkit-animation-delay: -1.2s;
          animation-delay: -1.2s;
}

.lds-grid div:nth-child(7) {
  top: 45px;
  left: 6px;
  -webkit-animation-delay: -0.8s;
          animation-delay: -0.8s;
}

.lds-grid div:nth-child(8) {
  top: 45px;
  left: 26px;
  -webkit-animation-delay: -1.2s;
          animation-delay: -1.2s;
}

.lds-grid div:nth-child(9) {
  top: 45px;
  left: 45px;
  -webkit-animation-delay: -1.6s;
          animation-delay: -1.6s;
}

@-webkit-keyframes lds-grid {
  0%,
  100% {
    opacity: 1;
  }

  50% {
    opacity: 0.5;
  }
}

@keyframes lds-grid {
  0%,
  100% {
    opacity: 1;
  }

  50% {
    opacity: 0.5;
  }
}

.PageLoading {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  width: 100%;
}

.card {
  min-width: 400px;
  background: #ffffff;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.1);
  border-radius: 8px;
  overflow: hidden;
  height: auto;
}

.card__header {
  padding: 0.5rem 0;
  height: 80px;
  background: #1b1b25;
  display: flex;
  justify-content: center;
}

.card__header-image {
  border-radius: 10px;
}
.card__section-name {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1rem 0;
}

.card__section-info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0.5rem 0;
  background: #f4f4f7;
}

.card__section-info h2 {
  margin: 0;
}

.card__avatar {
  border-radius: 50%;
  margin-right: 1rem;
  width: 120px;
  height: 120px;
}

.card__footer {
  height: 54px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: darkred;
  font-weight: bold;
  font-style: italic;
}

.form-group {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.cardNew {
  margin-bottom: 1rem;
}

.cardNew__hero {
  width: 100%;
  display: flex;
  justify-content: center;
  padding: 2rem 0;
  background: #1b1b25;
  background-image: url(/CardManager/static/media/stars.d4d2376b.svg);
  background-repeat: repeat;
  margin-bottom: 1rem;
  color: #ffffff;
}

.cardNew__hero img {
  border-radius: 10px;
}

.container__compsNew {
  width: 1000px;
  margin: 0 auto;
  display: grid;
  grid-template-columns: 1fr 1fr;
}

.container__compsNew .card {
  margin-right: 2rem;
}

.cardsList ul > li {
  margin-bottom: 1rem;
}

.cardsListItem {
  background: #ffffff;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.23);
  border-radius: 5px;
  display: flex;
  padding: 1rem;
}

.cardsListItem__avatar {
  border-radius: 50%;
  background: #a3a3a3;
  margin-right: 1rem;
  width: 80px;
  height: 80px;
}

.cardsListItem__main > p {
  margin: 0 0 0.5rem 0;
}

.twitter {
  color: #00acee;
}

.form-group {
  display: flex;
  justify-content: center;
}

.form-control {
  margin-top: 0.5rem;
  padding: 0.5rem;
  border-radius: 10px;
  border: none;
  box-shadow: 0 1px 10px 0 rgba(0, 0, 0, 0.4);
}

ul {
  padding: 0;
}

.PageError {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  width: 100%;
  font-size: 3rem;
}

.cards__hero {
  width: 100%;
  padding: 2rem 0;
  background: #1b1b25;
  background-repeat: repeat;
  margin-bottom: 1rem;
}

.cards__hero > .cards__container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.cards__container {
  width: 100%;
  max-width: 580px;
  margin: 1rem auto;
  padding: 0 1rem;
}

.cards_conf-logo {
  margin-bottom: 2rem;
  border-radius: 10px;
}

.cards__buttons {
  display: flex;
  justify-content: flex-end;
  margin-bottom: 1rem;
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.5);

  display: flex;
  flex-direction: column;
  align-items: center;
}

.modal__container {
  position: relative;
  top: 10rem;
  background-color: #ffffff;
  padding: 1rem;
  width: 400px;
}

.modal__close-button {
  position: absolute;
  top: 0;
  right: 0;
  border: 0;
  background-color: #f5f5f5;
  padding: 0.5rem 1rem;
  cursor: pointer;
}

