.card {
  min-width: 400px;
  background: #ffffff;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.1);
  border-radius: 8px;
  overflow: hidden;
  height: auto;
}

.card__header {
  padding: 0.5rem 0;
  height: 80px;
  background: #1b1b25;
  display: flex;
  justify-content: center;
}

.card__header-image {
  border-radius: 10px;
}
.card__section-name {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1rem 0;
}

.card__section-info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0.5rem 0;
  background: #f4f4f7;
}

.card__section-info h2 {
  margin: 0;
}

.card__avatar {
  border-radius: 50%;
  margin-right: 1rem;
  width: 120px;
  height: 120px;
}

.card__footer {
  height: 54px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: darkred;
  font-weight: bold;
  font-style: italic;
}
